import _ from 'lodash';
import { delegate, getNodes } from '@gravityforms/utils';
import { handleGridStateChange, setGridInitialState } from './grid-state';
import { handleQuickActions } from './quick-actions';
import { redirectToEntryEdit } from '../utils/url';

import instances from './instances';
import options from './options';
import state from './state';

/**
 * @function showQuickActions
 * @description Show the quick actions menu and focus it for key events.
 *
 * @since 2.8.0
 *
 * @param  e The ag grid event object
 */

const showQuickActions = ( e ) => {
	const delegateTarget = getNodes( 'gflow-quick-actions-trigger', false, e.event.target )[ 0 ];
	if ( ! delegateTarget ) {
		return;
	}

	handleQuickActions( {
		delegateTarget,
		key: 'Enter',
	} );
};

/**
 * @function handleEntryLinkEvents
 * @description Handles clicks on the entry details link in a grid cell and suppresses it for certain cases.
 *
 * @since 2.8.3
 *
 * @param {MouseEvent} e The ag grid event object
 */

const handleEntryLinkEvents = ( e ) => {
	if ( state.dragging ) {
		e.preventDefault();
	}
};

/**
 * @function gridCellClicked
 * @description Handles redirecting to the entry detail on grid cell click.
 *
 * @since 2.8.0
 *
 * @param  e The ag grid event object
 */

const gridCellClicked = ( e ) => {
	if ( state.dragging || e.column.colId === 'actions' || ! state.openInNewTab || e.event.type === 'click' ) {
		return;
	}
	redirectToEntryEdit( e.data.url_entry );
};

/**
 * @function gridCellKeyEvents
 * @description Handles key events for the cells.
 *
 * @since 2.8.0
 *
 * @param  e The ag grid event object
 */

const gridCellKeyEvents = ( e ) => {
	if ( e.event.key !== 'Enter' ) {
		return;
	}
	if ( e.column.colId === 'actions' ) {
		showQuickActions( e );
		return;
	}
	redirectToEntryEdit( e.data.url_entry );
};

/**
 * @function gridReady
 * @description Handle the grid initialized event
 *
 * @since @since 2.8.0
 *
 * @param {Event}  event  The ag grid event object
 * @param {string} gridId The ag grid id string
 */

const gridReady = ( event, gridId ) => {
	setGridInitialState( gridId );
};

/**
 * @function bindGridEvents
 * @description Setup all grid events for this instance.
 *
 * @since 2.8.0
 *
 * @param {string} gridId Id of the grid
 */

const bindGridEvents = ( gridId ) => {
	const grid = getNodes( `[data-grid-id="${ gridId }"]`, false, document, true )[ 0 ];
	instances.gridOptions[ gridId ].onGridReady = ( e ) =>
		gridReady( e, gridId );
	instances.gridOptions[ gridId ].onCellClicked = ( e ) =>
		gridCellClicked( e );
	instances.gridOptions[ gridId ].onCellKeyPress = ( e ) =>
		gridCellKeyEvents( e );
	options.gridMutationEvents.forEach( ( functionName ) => {
		instances.gridOptions[ gridId ][ functionName ] = _.debounce(
			( e ) => handleGridStateChange( e, gridId ),
			200,
			false
		);
	} );

	delegate( grid, '[data-js="inbox-details-link"]', 'click', handleEntryLinkEvents );
};

export default bindGridEvents;
