/**
 * Inbox
 *
 * Initializes the inbox component
 */

import draggableHandler from './draggable-handler';
import filterHandler from './filter-handler';
import fullscreenHandler from './fullscreen-handler';
import searchHandler from './search-handler';
import initializeGrid from './initialize-grid';
import quickActions from './quick-actions';
import notifications, { checkNotificationStatus } from './notifications';
import gridRefresh from './grid-refresh';
import gridState from './grid-state';
import settings from './settings';

import el from './elements';

/**
 * @function initializeGrids
 * @description Iterate over all found grids and initialize.
 *
 * @since 2.8.0
 *
 */

const initializeGrids = () => {
	el.containers.forEach( ( grid, index ) => initializeGrid( grid, index ) );
};

/**
 * @function init
 * @description Kick off this modules functionality.
 *
 * @since 2.8.0
 *
 */

const init = ( containers ) => {
	el.containers = containers;

	initializeGrids();
	gridRefresh();
	gridState();
	quickActions();
	settings();
	fullscreenHandler();
	filterHandler();
	searchHandler();
	draggableHandler();
	notifications();
	checkNotificationStatus();

	console.info( 'Gravity Flow Common: Initialized inbox components.' );
};

export default init;
