import gflowConfig from 'gflow-config';
import ifVisible from 'ifvisible.js';
import { savePref } from '../utils/requests';
import { getClosest, checkNotificationPromise, getNodes } from '@gravityforms/utils';

import instances from './instances';
import state from './state';

const data = gflowConfig?.grids || {};
const i18n = gflowConfig?.i18n || {};

/**
 * @function handleUpdateCounts
 * @description On refresh handles checking if there are new rows to notify about. and collates for all tables on state
 * variable newRowsOnUpdate.
 *
 * @since 2.8.0
 *
 * @param  gridId {number} The grid id.
 * @param  update {array} The update data in array.
 */

export const handleUpdateCounts = ( gridId, update ) => {
	const api = instances.gridOptions[ gridId ].api;
	let currentCount = 0;
	api.forEachNode( () => {
		currentCount++;
	} );
	const newCount =
		update.add.length - update.remove.length + update.update.length;

	state.newRowsOnUpdate = state.newRowsOnUpdate + ( newCount - currentCount );
};

/**
 * @function maybePushNotify
 * @description Checks for needed data and then notifies if all cases are met.
 *
 * @since 2.8.0
 *
 * @param  gridId {number} The grid id.
 */

const maybePushNotify = ( gridId ) => {
	if ( ! gridId ) {
		return;
	}
	if ( ! data[ gridId ].push_enabled ) {
		return;
	}

	const currentCount = instances.gridOptions[
		gridId
	].api.getDisplayedRowCount();

	if ( currentCount === state.previousRowCount ) {
		return;
	}

	const addArray = state.latestUpdateData.add[ 0 ];
	const id = `noti-${ addArray.id }`;

	instances.notifications[ id ] = new window.Notification(
		i18n.inbox_notification_title,
		{
			body: i18n.new_inbox_items,
		}
	);
	state.previousRowCount = currentCount;

	instances.notifications[ id ].addEventListener(
		'click',
		( e ) => {
			if ( state.latestUpdateData.add.length > 1 ) {
				window.parent.focus();
				window.focus();
				e.target.close();
			} else {
				window.open( addArray.url_entry, id ).focus();
			}
		},
		false
	);
};

/**
 * @function maybeHandleNotifications
 * @description If the tab is not focused, update doc title and maybe send push notification if enabled to user.
 *
 * @since 2.8.0
 *
 * @param  gridId {number} The grid id.
 */

export const maybeHandleNotifications = ( gridId = null ) => {
	// tab visible, set to zero and bail
	if ( ifVisible.now() ) {
		state.newRowsOnUpdate = 0;
		document.title = state.cleanDocTitle;
		return;
	}
	// tab is not focused, update title with counts to give indicator of new entries
	if ( state.newRowsOnUpdate > 0 ) {
		document.title = `(${ state.newRowsOnUpdate }) ${ state.cleanDocTitle }`;
		maybePushNotify( gridId );
	} else {
		document.title = state.cleanDocTitle;
	}
};

/**
 * @function handlePermission
 * @description Saves the push notification preference for a grid id.
 *
 * @since 2.8.0
 *
 * @param  permission {string} The permission string.
 * @param  gridId     {number} The grid id.
 */

const handlePermission = ( permission, gridId ) => {
	if ( permission === 'granted' ) {
		savePref( 'push_enabled', true, 'view', gridId );
		data[ gridId ].push_enabled = true;
	} else {
		savePref( 'push_enabled', false, 'view', gridId );
		data[ gridId ].push_enabled = false;
	}
};

/**
 * @function handlePushNotificationsToggle
 * @description Handles requesting permission for notifications.
 *
 * @since 2.8.0
 *
 * @param  input {HTMLInputElement} The toggle element.
 */

export const handlePushNotificationsToggle = ( input ) => {
	if ( ! ( 'Notification' in window ) ) {
		alert( i18n.browser_notifications_error ); // eslint-disable-line
		return;
	}
	const gridContainerParent = getClosest( input, '.gform-flyout' )
		.previousElementSibling;
	const gridContainer = getNodes( 'gflow-inbox', false, gridContainerParent )[ 0 ];
	if ( input.checked ) {
		if ( checkNotificationPromise() ) {
			window.Notification.requestPermission().then( ( permission ) => {
				handlePermission( permission, gridContainer.dataset.gridId );
			} );
		} else {
			window.Notification.requestPermission( ( permission ) => {
				handlePermission( permission, gridContainer.dataset.gridId );
			} );
		}
	} else {
		savePref( 'push_enabled', false, '', gridContainer.dataset.gridId );
		data[ gridContainer.dataset.gridId ].push_enabled = false;
	}
};

/**
 * @function checkNotificationStatus
 * @description If push notifications were set to true, confirm this session has notification permissions.
 *
 * @since 2.8.0
 *
 */

export const checkNotificationStatus = () => {
	if ( ! state.pushEnabled || typeof window.Notification === 'undefined' ) {
		return;
	}
	if ( window.Notification.permission === 'granted' ) {
		return;
	}
	window.Notification.requestPermission();
};

/**
 * @function bindEvents
 * @description Bind event handlers.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	ifVisible.on( 'focus', () => {
		state.notificationShown = false;
		maybeHandleNotifications();
	} );

	ifVisible.on( 'blur', () => {
		state.openInNewTab = false;
	} );
};

/**
 * @function init
 * @description Kick off this module's functionality.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
};

export default init;
