import gflowConfig from 'gflow-config';
import { delegate } from '@gravityforms/utils';
import { handlePushNotificationsToggle } from './notifications';
import * as gridTemplates from 'templates/components/grid';
import instances from './instances';
import Flyout from '@gravityforms/components/html/admin/Flyout';
import * as inboxTemplates from 'templates/grids/inbox';

const data = gflowConfig?.grids || {};
const i18n = gflowConfig?.i18n || {};

/**
 * @function initializeSettings
 * @description Inject the settings trigger and instantiate the flyout.
 *
 * @since 2.8.0
 *
 * @param  grid      {HTMLElement} The grid element.
 * @param  gridId    {string} The grid id.
 * @param  gridIndex {number} The grid index on the page.
 */

export const initializeSettings = ( grid, gridId, gridIndex ) => {
	grid.insertAdjacentHTML(
		'afterbegin',
		gridTemplates.settingToggle(
			'inbox-settings',
			i18n.toggle_settings_title,
			gridId
		)
	);

	grid.insertAdjacentHTML(
		'afterbegin',
		gridTemplates.fullscreenToggle(
			'inbox-fullscreeen',
			i18n.toggle_fullscreen_title
		)
	);

	grid.insertAdjacentHTML(
		'afterbegin',
		gridTemplates.clearFiltersToggle(
			'inbox-clear-filters',
			i18n.toggle_clear_filters_title
		)
	);

	instances.flyouts[ `${ gridId }-${ gridIndex }` ] = new Flyout( {
		content: inboxTemplates.settings( data[ gridId ], `${ gridId }-${ gridIndex }` ),
		maxWidth: 650,
		position: 'absolute',
		target: `.gflow-inbox.gflow-grid[data-grid-id="${ gridId }"]`,
		title: i18n.inbox_settings,
		triggers: `[data-js="inbox-settings"][data-grid-id="${ gridId }"]`,
		wrapperClasses: 'gform-flyout gform-flyout--inbox-settings',
	} );
};

/**
 * @function handleSettingsChange
 * @description Handle changes to the settings for the inbox.
 *
 * @since 2.8.0
 *
 * @param  e {Event} The event object.
 */

const handleSettingsChange = ( e ) => {
	// switch based on setting name to handle cases, name is also value stored and passed in on init from php in config
	if ( e.delegateTarget.name === 'inbox-setting--push-enabled' ) {
		handlePushNotificationsToggle( e.delegateTarget );
	}
};

/**
 * @function bindEvents
 * @description Bind event handlers.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	delegate(
		document.body,
		'[data-js="inbox-setting"]',
		'click',
		handleSettingsChange
	);
};

/**
 * @function init
 * @description Kick off this module's functionality.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
};

export default init;
