/**
 * @function intComparator
 * @description Function used by ag grid when sorting numeric columns
 *
 * @since 2.8.0
 *
 * @param {...*} args The args sent to this method, which are
 *                    {String}  The current column object.
 *                    {String}  The first value to compare.
 *                    {String}  The second value to compare.
 *                    {Object}  The node representing the first row.
 *                    {Object}  The node representing the second row.
 *                    {Boolean} Whether the current sort is inverted.
 *
 * @return {number} Either -1, 0, or 1.
 */

export const intComparator = ( ...args ) => {
	const value1 = args[ 1 ];
	const value2 = args[ 2 ];

	return Math.sign( parseInt( value1 ) - parseInt( value2 ) );
};

/**
 * @function dateComparator
 * @description Function used by ag grid when sorting date columns
 *
 * @since 2.8.0
 *
 * @param {...*} args The args sent to this method, which are
 *                    {String} The current column object.
 *                    {Date}   A Date Object representing the selected Calendar date at Midnight.
 *                    {String} The current cell value being compared.
 *
 * @return {number} Either -1, 0, or 1.
 */

export const dateComparator = ( ...args ) => {
	const dateOne =
		typeof args[ 1 ] === 'object'
			? args[ 1 ]
			: new Date( args[ 1 ] * 1000 );
	const dateTwo = new Date( args[ 2 ] * 1000 );

	let response = 0;

	dateTwo.setDate( dateTwo.getUTCDate() );
	dateTwo.setMonth( dateTwo.getUTCMonth() );

	dateOne.setHours( 0, 0, 0, 0 );
	dateTwo.setHours( 0, 0, 0, 0 );

	if ( dateTwo < dateOne ) {
		response = -1;
	} else if ( dateTwo > dateOne ) {
		response = 1;
	}

	// This is a filter call, not a sort call. Return unmodified result.
	if ( typeof args[ 5 ] === 'undefined' ) {
		return response;
	}

	// Sort calls need to work in the opposite direction, reverse the result.
	return response * -1;
};

/**
 * @function humanReadableGetter
 * @description Get human-readable text from displayKey.
 *
 * @since 2.8.0
 *
 * @param {object} params The current params for the column.
 *
 * @return {*} The human-readable value.
 */

export const humanReadableGetter = ( params ) => {
	const displayKey = params.colDef.displayKey;
	return params.data[ displayKey ];
};

/**
 * @function applyColumnComparator
 * @description Apply a comparator based on column type. String or int.
 *
 * @since 2.8.0
 *
 * @param {object} column
 * @return {*}
 */

export const applyColumnComparator = ( column ) => {
	column.valueFormatter = humanReadableGetter;

	if ( column.compareType === 'string' ) {
		return column;
	}

	if ( column.compareType === 'date' ) {
		column.comparator = dateComparator.bind( null, column );
		column.filterParams = {
			comparator: dateComparator.bind( null, column ),
			inRangeInclusive: true,
		};
		column.filter = 'agDateColumnFilter';

		return column;
	}

	column.comparator = intComparator.bind( null, column );

	return column;
};
