import gflowConfig from 'gflow-config';
import { uniqueId } from '@gravityforms/utils';
import input from 'templates/components/input';
import toggle from 'templates/components/toggle';
import {
	QUICK_ACTION_SHOW_NOTE,
	QUICK_ACTION_KEY,
	QUICK_ACTION_LABEL,
} from 'common/config/constants';

const i18n = gflowConfig?.i18n || {};

/**
 * @function settings
 * @description The content for the inbox settings flyout.
 *
 * @since 2.8.0
 *
 * @param  data {object} Data for the template.
 * @param  id   {string} The id for the grid plus index.
 *
 * @return {string}
 */

export const settings = ( data = {}, id = '' ) =>
	`
	<span class="gform-flyout__setting-label">
		${ i18n.settings_push_title }
	</span>
	<span class="gform-flyout__setting-desc">
		${ i18n.settings_push_desc }
	</span>
	${ toggle(
		`inbox-setting--push-enabled-${ id }`,
		'inbox-setting--push-enabled',
		data.push_enabled,
		i18n.disabled,
		i18n.enabled,
		'gform-field__toggle gform-flyout__setting',
		'inbox-setting'
	) }
	`;

/**
 * @function header
 * @description The inbox header with search.
 *
 * @since 2.8.0
 *
 * @return {string}
 */

export const header = ( searchId = uniqueId( 'inbox-header-' ) ) =>
	`
	<div class="gflow-grid__header">
		<i class="gflow-grid__search-icon gflow-icon gflow-icon--search"></i>
		${ input(
		'search',
		searchId,
		'',
		'',
		'gform-input gflow-inbox__search',
		'gflow-inbox-search',
		i18n.search_inbox
	) }
	</div>
	`;

/**
 * @function actionsTrigger
 * @description The inbox trigger for quick actions menu.
 *
 * @since 2.8.0
 *
 * @param  entryId  {number} The associated entry id
 * @param  entryUrl {string} The entry url
 *
 * @return {string}
 */

export const actionsTrigger = ( entryId = 0, entryUrl ) =>
	`<button 
		class="gflow-grid__actions-trigger gflow-icon gflow-icon--more-vertical" 
		aria-label="${ i18n.view_quick_actions }"
		data-js="gflow-quick-actions-trigger"
		data-id="${ entryId }"
		data-url="${ entryUrl }"
	></button>`;

/**
 * @function actionsMenu
 * @description The quick actions menu for an entry.
 *
 * @since 2.8.0
 *
 * @param  entryUrl {string} The entry url.
 * @param  actions  {array} Action info like label, key and note reqs.
 *
 * @return {string}
 */

export const actionsMenu = ( entryUrl = '', actions = [] ) =>
	`<article class="gform-admin gflow-grid__actions-menu" data-js="gflow-quick-actions-menu">
		<div class="gflow-grid__actions-menu-row">
			<button 
				class="gflow-grid__actions-menu-trigger" 
				data-js="gflow-quick-actions-menu-trigger" 
				data-action="edit"
				data-url="${ entryUrl }"
			>
				${ i18n.view_entry }
			</button>
		</div>
		${ actions
		.map(
			( action ) => `
					<div class="gflow-grid__actions-menu-row">
						<button 
							class="gflow-grid__actions-menu-trigger gflow-grid__actions-menu-trigger--${ action[ QUICK_ACTION_KEY ] }"
							data-js="gflow-quick-actions-menu-trigger" 
							data-action="${ action[ QUICK_ACTION_KEY ] }"
							data-requires-note="${ action[ QUICK_ACTION_SHOW_NOTE ] }"
						>
							${ action[ QUICK_ACTION_LABEL ] }
							<span class="gform-loader--spin">${ i18n.loading }</span>
							<i class="gflow_grid__actions-icon-success gflow-icon gflow-icon--check"></i>
						</button>
					</div>`
		)
		.join( '' ) }
		<div class="gflow-grid__actions-menu-note">
			<span class="gflow-grid__actions-menu-heading">${ i18n.actions_menu_heading }</span>
			<textarea class="gflow-grid__actions-menu-textarea" data-js="gflow-quick-actions-menu-note"></textarea>
			<div class="gflow-grid__actions-menu-footer">
				<button 
					class="gform-button gform-button--white gform-button--size-sm gflow_grid__actions-menu-submit" 
					data-js="gflow-quick-actions-menu-note-action" 
					data-action="submit"
					disabled="disabled"
				>
					${ i18n.submit }
					<span class="gform-loader--spin">${ i18n.loading }</span>
					<i class="gflow_grid__actions-icon-success gflow-icon gflow-icon--check"></i>
				</button>
				<button 
					class="gform-button gform-button--white gform-button--size-sm gform-button--no-border" 
					data-js="gflow-quick-actions-menu-note-action"
					data-action="cancel"
				>${ i18n.cancel }</button>
			</div>
		</div>
	</article>`;

/**
 * @function loadingMask
 * @description Masks the inbox during ajax to prevent interactions.
 *
 * @since 2.8.0
 *
 * @return {string}
 */

export const loadingMask = () => `
	<div class="gflow-inbox__loading-mask" data-js="gform-loading-mask"></div>
`;
