/**
 * @class CellLinkRenderer
 * @description Injects the link into each cell for the workflow detail page.
 *
 * @since 2.8.3
 *
 * @type {CellLinkRenderer}
 */
export const cellLinkRenderer = class CellLinkRenderer {
	init( params ) {
		this.eGui = document.createElement( 'div' );
		this.eGui.innerHTML = `
			<span>${ params.valueFormatted }</span>
			<a 
				class="gflow-inbox__entry-cell-link" 
				data-js="inbox-details-link" 
				href="${ params.node.data.url_entry }" 
				tabindex="-1"
			></a>
		`;
	}

	getGui() {
		return this.eGui;
	}
};
