import { openNewTab } from '@gravityforms/utils';

import state from '../inbox/state';

/**
 * @function redirectToEntryEdit
 * @description Executes redirect to entry edit.
 *
 * @since 2.8.0
 *
 * @param  url {string} The entry url.
 */

export const redirectToEntryEdit = ( url = '' ) => {
	if ( state.openInNewTab ) {
		openNewTab( url );
	} else {
		window.location.href = url;
	}
};
