import { Grid } from 'ag-grid-community';
import AgGridDateComponent from 'common/components/ag-grid-date';
import gflowConfig from 'gflow-config';
import { getNodes, localStorage } from '@gravityforms/utils';
import bindGridEvents from './grid-events';
import { applyColumnComparator } from '../utils/comparators';
import { getGridId, getGridOptions } from '../utils/data';
import { cellLinkRenderer } from './cell-renderer';
import { actionsRenderer } from './quick-actions';
import { initializeSettings } from './settings';
import * as inboxTemplates from 'templates/grids/inbox';

import instances from './instances';
import state from './state';
import locale from './locale';
import options from './options';

const data = gflowConfig?.grids || {};
const i18n = gflowConfig?.i18n || {};

/**
 * @function initializeSearch
 * @description Inject the search input for the whole table instance.
 *
 * @since 2.8.0
 *
 */

const initializeSearch = ( grid ) => {
	grid.insertAdjacentHTML( 'afterbegin', inboxTemplates.header() );
};

/**
 * @function handleLocalStorage
 * @description When users arent logged in we store settings in localstorage.
 * Before each grid initializes we check if they have stored data and modify the config
 * coming from the server.
 *
 * @since 2.8.3
 *
 * @param {string} gridId The id of the grid.
 *
 * @return {void}
 */

const handleLocalStorage = ( gridId = '' ) => {
	// logged in user, bail
	if ( gflowConfig.is_user_logged_in ) {
		return;
	}
	// handles bools for now, as we get more complex with stores we'll have to get into type handling here
	options.localStorageSettings.forEach( ( opt ) => {
		const localData = localStorage.get( `${ gridId }_setting_${ opt }` );
		if ( ! localData ) {
			data[ gridId ][ opt ] = false;
			return;
		}
		data[ gridId ][ opt ] = localData === 'true';
	} );
};

/**
 * @function initializeGrid
 * @description Initializes an ag grid instance on the passed containing div.
 *
 * @since 2.8.0
 *
 * @param {HTMLElement} grid      The grid container.
 * @param {number}      gridIndex The index of the grid on the page.
 */

const initializeGrid = ( grid, gridIndex ) => {
	const gridId = getGridId( grid );

	if ( ! data[ gridId ]?.grid_options ) {
		console.error( `${ i18n.options_for_grid_not_found } ${ gridId }` );
	}

	handleLocalStorage( gridId );

	grid.setAttribute( 'data-grid-index', gridIndex );
	instances.gridOptions[ gridId ] = getGridOptions( gridId );

	if ( data[ gridId ].push_enabled ) {
		state.pushEnabled = true;
	}

	instances.gridOptions[
		gridId
	].overlayNoRowsTemplate = `<span class="ag-overlay-no-rows-center">${ i18n.no_entries }</span>`;

	bindGridEvents( gridId );

	// set locale and translations up
	instances.gridOptions[ gridId ].localeText = locale;

	// apply special class to cells containing html (field based cols only)
	const cellClassRules = {
		'gflow-inbox__html-cell': ( params ) => /<[a-z/][\s\S]*>/i.test( params.value ),
	};
	instances.gridOptions[ gridId ].columnDefs.forEach( ( colDef ) => {
		if ( colDef.cellClassRules ) {
			colDef.cellClassRules = cellClassRules;
		}
	} );

	instances.gridOptions[ gridId ].getRowNodeId = ( row ) => {
		return parseInt( row.id );
	};

	instances.gridOptions[ gridId ].components = {
		actionsRenderer,
		cellLinkRenderer,
		agDateInput: AgGridDateComponent,
	};

	// set highlight border on left if in data
	instances.gridOptions[ gridId ].getRowStyle = ( params ) => {
		if ( params.data.step_highlight ) {
			return { borderLeft: `3px solid ${ params.data.step_highlight }` };
		}
	};

	instances.gridOptions[ gridId ].columnDefs.forEach( ( column, index ) => {
		instances.gridOptions[ gridId ].columnDefs[
			index
		] = applyColumnComparator( column );
	} );

	instances.grids[ gridId ] = new Grid(
		grid,
		instances.gridOptions[ gridId ]
	);

	initializeSearch( grid );
	initializeSettings( grid, gridId, gridIndex );

	setTimeout( () => {
		grid.classList.add( 'gflow-inbox--reveal' );
		grid.parentElement.classList.add( 'gflow-inbox--reveal' );

		setTimeout( () => {
			const loader = getNodes( 'gflow-inbox-loader', false, grid.parentElement.parentElement )[ 0 ];
			loader.parentNode.removeChild( loader );
		}, 200 );
	}, 300 );
};

export default initializeGrid;
