import gflowConfig from 'gflow-config';
import { INBOX_DEFAULT_ID } from 'common/config/constants';
import { getClosest } from '@gravityforms/utils';
import instances from '../inbox/instances';

const globalOptions = {
	animateRows: false,
};
const data = gflowConfig?.grids || {};

/**
 * @function getIdsFromModel
 * @description Get all ids for grid rows from a grid.
 *
 * @since 2.8.0
 *
 * @param  gridId {number} The grid id
 * @return {*[]}
 */

export const getIdsFromModel = ( gridId ) => {
	const ids = [];
	instances.gridOptions[ gridId ].api.forEachNode( ( node ) =>
		ids.push( node.data.id )
	);
	return ids;
};

/**
 * @function getGridIDFromEvent
 * @description Returns a Grid ID from the given input Event.
 *
 * @since 2.8.0
 *
 * @param  e The input Event.
 *
 * @return {string}
 */

export const getGridIdFromEvent = ( e ) => {
	const input = e.delegateTarget;
	const gridContainer = getClosest( input, '[data-js="gflow-inbox"]' );
	return getGridId( gridContainer );
};

/**
 * @function getGridId
 * @description Gets the grid id used to match options to instances in the dom.
 *
 * @since 2.8.0
 *
 * @param {HTMLElement} grid The wrapping container the grid is injected to
 * @return {string}
 */

export const getGridId = ( grid ) => grid.dataset.gridId || INBOX_DEFAULT_ID;

/**
 * @function getGridOptions
 * @description Merges global options with the instance specific options for a grid instance.
 *
 * @since 2.8.0
 *
 * @param {string} gridId The unique identifier for the grid instance.
 * @return {object}
 */

export const getGridOptions = ( gridId = INBOX_DEFAULT_ID ) =>
	Object.assign( {}, data[ gridId ].grid_options, globalOptions );

/**
 * @function deleteRow
 * @description deletes a row from an ag grid instance by entryId.
 *
 * @since 2.8.0
 *
 * @param {object} gridOptions
 * @param {number} entryId
 */

export const deleteRow = ( gridOptions = {}, entryId = 0 ) => {
	gridOptions.api.forEachNode( ( node ) => {
		if ( Number( node.data.id ) === Number( entryId ) ) {
			gridOptions.rowData.splice( node.rowIndex, 1 );
			gridOptions.api.setRowData( gridOptions.rowData );
		}
	} );
};
