import { delegate } from '@gravityforms/utils';
import { getGridIdFromEvent } from '../utils/data';

import instances from './instances';

/**
 * @function applySearch
 * @description Apply a search term to the grid.
 *
 * @since 2.8.0
 *
 * @param  e The event object.
 */

const applySearch = ( e ) => {
	const input = e.delegateTarget;
	const gridId = getGridIdFromEvent( e );
	instances.gridOptions[ gridId ].api.setQuickFilter( input.value );
};

/**
 * @function clearSearch
 * @description Clear the search term from the grid.
 *
 * @since 2.8.0
 *
 * @param  e The event object.
 */

const clearSearch = ( e ) => {
	const gridId = getGridIdFromEvent( e );
	instances.gridOptions[ gridId ].api.setQuickFilter( null );
};

/**
 * @function bindEvents
 * @description Bind event handlers.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	delegate(
		document.body,
		'[data-js="gflow-inbox-search"]',
		'keyup',
		applySearch
	);

	delegate(
		document.body,
		'[data-js="gflow-inbox-search"]',
		'search',
		clearSearch
	);
};

/**
 * @function init
 * @description Kick off this module's functionality.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
};

export default init;
