/**
 * @function settingToggle
 * @description The gear icon for an ag grid instance that will trigger the settings flyout.
 *
 * @since 2.8.0
 *
 * @param  triggerId {string} The id for the trigger.
 * @param  title     {string} The a11y title for the trigger.
 * @param  gridId    {string} The grid id.
 *
 * @return {string}
 */

export const settingToggle = ( triggerId = 'grid-settings', title = '', gridId = '' ) =>
	`
	<button 
		class="gflow-grid__button gflow-grid__button--settings gflow-icon gflow-icon--settings" 
		data-js="${ triggerId }" 
		data-grid-id="${ gridId }"
		title="${ title }"
	></button>
	`;

/**
 * @function fullscreenToggle
 * @description The fullscreen icon for making a table go full screen.
 *
 * @since 2.8.0
 *
 * @param  triggerId {string} The id for the trigger.
 * @param  title     {string} The a11y title for the trigger.
 *
 * @return {string}
 */

export const fullscreenToggle = ( triggerId = 'grid-fullscreen', title = '' ) =>
	`
	<button 
		class="gflow-grid__button gflow-grid__button--fullscreen gflow-icon gflow-icon--maximize-2" 
		data-js="${ triggerId }" 
		title="${ title }"
	></button>
	`;

/**
 * @function clearFiltersToggle
 * @description The button to clear filters that are active.
 *
 * @since 2.8.0
 *
 * @param  triggerId {string} The id for the trigger.
 * @param  title     {string} The a11y title for the trigger.
 *
 * @return {string}
 */

export const clearFiltersToggle = ( triggerId = 'grid-clear-filters', title = '' ) =>
	`
	<button 
		class="gflow-grid__button gflow-grid__button--clear-filters gflow-icon gflow-icon--trash" 
		data-js="${ triggerId }" 
		title="${ title }"
	></button>
	`;
