import { bodyLock, delegate, getNodes } from '@gravityforms/utils';

const state = {
	activeGrid: null,
};

/**
 * @function exitFullscreen
 * @description Exit fullscreen mode.
 *
 * @since 2.8.0
 *
 */

const exitFullscreen = () => {
	document.body.classList.remove( 'gflow-grid--fullscreen-active' );
	bodyLock.unlock();

	state.activeGrid.style.left = `${ state.activeGrid.dataset.left }px`;
	state.activeGrid.style.top = `${ state.activeGrid.dataset.top }px`;
	state.activeGrid.style.width = `${ state.activeGrid.dataset.width }px`;
	state.activeGrid.style.height = `${ state.activeGrid.dataset.height }px`;

	setTimeout( () => {
		const placeholder = getNodes( 'gflow-grid-placeholder' )[ 0 ];
		placeholder.parentNode.removeChild( placeholder );
		state.activeGrid.classList.remove( 'gflow-grid--animate' );
		state.activeGrid.classList.remove( 'gflow-grid--fullscreen' );
		state.activeGrid.style.left = ``;
		state.activeGrid.style.top = ``;
		state.activeGrid.style.width = ``;
		state.activeGrid.style.height = ``;
	}, 400 );
};

/**
 * @function enterFullscreen
 * @description Enter fullscreen mode.
 *
 * @since 2.8.0
 *
 */

const enterFullscreen = () => {
	const rect = state.activeGrid.getBoundingClientRect();
	state.activeGrid.insertAdjacentHTML(
		'beforebegin',
		`<div data-js="gflow-grid-placeholder" style="height: ${ rect.height }px"></div>`
	);

	state.activeGrid.setAttribute( 'data-top', rect.top );
	state.activeGrid.setAttribute( 'data-left', rect.left );
	state.activeGrid.setAttribute( 'data-height', rect.height );
	state.activeGrid.setAttribute( 'data-width', rect.width );

	state.activeGrid.style.left = `${ rect.left }px`;
	state.activeGrid.style.top = `${ rect.top }px`;
	state.activeGrid.style.width = `${ rect.width }px`;
	state.activeGrid.style.height = `${ rect.height }px`;

	state.activeGrid.classList.add( 'gflow-grid--fullscreen' );

	setTimeout( () => {
		const top = document.body.classList.contains( 'admin-bar' )
			? 32
			: 0;
		state.activeGrid.classList.add( 'gflow-grid--animate' );
		state.activeGrid.style.left = `0`;
		state.activeGrid.style.top = `${ top }px`;
		state.activeGrid.style.width = `100%`;
		state.activeGrid.style.height = `calc(100% - ${ top }px)`;
	}, 25 );

	setTimeout( () => {
		document.body.classList.add( 'gflow-grid--fullscreen-active' );
		bodyLock.lock( false );
	}, 400 );
};

/**
 * @function handleFullscreen
 * @description Toggle fullscreen mode for the active inbox.
 *
 * @since 2.8.0
 *
 */

const handleFullscreen = ( e ) => {
	state.activeGrid = e.delegateTarget.parentNode.parentNode.parentNode;
	if ( state.activeGrid.classList.contains( 'gflow-grid--fullscreen' ) ) {
		exitFullscreen();
	} else {
		enterFullscreen();
	}
};

/**
 * @function handleKeyEvents
 * @description Handle escape key to close the window when that key is pressed.
 *
 * @since 2.8.0
 *
 * @param  e {KeyboardEvent} The event object.
 */

const handleKeyEvents = ( e ) => {
	if ( ! state.activeGrid || e.key !== 'Escape' ) {
		return;
	}
	if ( ! state.activeGrid.classList.contains( 'gflow-grid--fullscreen' ) ) {
		return;
	}

	exitFullscreen();
};

/**
 * @function bindEvents
 * @description Bind event handlers.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	delegate(
		document.body,
		'[data-js="inbox-fullscreeen"]',
		'click',
		handleFullscreen
	);

	document.addEventListener( 'keydown', handleKeyEvents );
};

/**
 * @function init
 * @description Kick off this module's functionality.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
};

export default init;
