import { delegate } from '@gravityforms/utils';
import instances from './instances';

/**
 * @function handleClearFilters
 * @description Clear all filters active for table.
 *
 * @since 2.8.0
 *
 * @param  e The event object.
 */

const handleClearFilters = ( e ) => {
	const gridId = e.delegateTarget.parentNode.dataset.gridId;
	instances.gridOptions[ gridId ].api.setFilterModel( null );
};

/**
 * @function bindEvents
 * @description Binds the events for this module.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	delegate(
		document.body,
		'[data-js="inbox-clear-filters"]',
		'click',
		handleClearFilters
	);
};

/**
 * @function init
 * @description Kick off this modules functionality
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
};

export default init;
