import { dragHorizontal, getNodes } from '@gravityforms/utils';

import el from './elements';
import state from './state';

/**
 * @function initializeDraggable
 * @description Sets up the draggable handlers that allow horizontal scroll with mouse drag.
 *
 * @since 2.8.0
 *
 */

// eslint-disable-next-line no-unused-vars
const initializeDraggable = () => {
	el.containers.forEach( ( grid ) => {
		const target = getNodes(
			'.ag-center-cols-viewport',
			false,
			grid,
			true
		)[ 0 ];
		dragHorizontal( target );
	} );
};

/**
 * @function bindEvents
 * @description Binds the events for this module.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	document.addEventListener( 'gform-utils/horizontal-drag-started', () => {
		state.dragging = true;
	} );
	document.addEventListener( 'gform-utils/horizontal-drag-ended', () => {
		setTimeout( () => {
			state.dragging = false;
		}, 200 );
	} );
};

/**
 * @function init
 * @description Kick off this modules functionality
 *
 * @since 2.8.0
 *
 */

const init = () => {
	//initializeDraggable();
	bindEvents();
};

export default init;
