import flatpickr from 'flatpickr';
import gflowConfig from 'gflow-config';

const defaultDateFormat = gflowConfig?.default_date_format || 'd/m/Y';

/**
 * @class AgGridDateComponent
 * @description Overrides ag grids internal date component for filtering with a flatpickr instance.
 *
 * @since 2.8.0
 *
 */

export default class AgGridDateComponent {
	init( params ) {
		const template = `
			<input type="text" data-input style="width: 100%;" />
			<a class="ag-grid__date-toggle" title="toggle" data-toggle>
		        <i class="gflow-icon gflow-icon--calendar"></i>
		    </a>
		
		    <a class="ag-grid__date-clear-toggle" title="clear" data-clear>
		        <i class="gflow-icon gflow-icon--x"></i>
		    </a>
		`;

		this.params = params;

		this.eGui = document.createElement( 'div' );
		this.eGui.setAttribute( 'role', 'presentation' );
		this.eGui.classList.add( 'ag-input-wrapper' );
		this.eGui.classList.add( 'custom-date-filter' );
		this.eGui.innerHTML = template;

		this.eInput = this.eGui.querySelector( 'input' );

		this.picker = flatpickr( this.eGui, {
			onChange: this.onDateChanged.bind( this ),
			dateFormat: defaultDateFormat,
			wrap: true,
		} );

		this.picker.calendarContainer.classList.add(
			'ag-custom-component-popup'
		);

		this.date = null;
	}

	getGui() {
		return this.eGui;
	}

	onDateChanged( selectedDates ) {
		this.date = selectedDates[ 0 ] || null;
		this.params.onDateChanged();
	}

	getDate() {
		return this.date;
	}

	setDate( date ) {
		this.picker.setDate( date );
		this.date = date;
	}

	setInputPlaceholder() {
		this.eInput.setAttribute( 'placeholder', defaultDateFormat );
	}
}
