import gflowConfig from 'gflow-config';
import request from '../../../utils/request';
import { handleUpdateCounts, maybeHandleNotifications } from './notifications';
import { getIdsFromModel } from '../utils/data';

import state from './state';
import instances from './instances';
import el from './elements';

const data = gflowConfig?.grids || {};

/**
 * @function refreshGrid
 * @description Refreshes data for the grid on a set interval, adding new entries to the stack.
 *
 * @since 2.8.0
 *
 * @param  gridId {string} The id of the grid.
 * @return {Promise<void>}
 */

const refreshGrid = async ( gridId ) => {
	const current_ids = getIdsFromModel( gridId ); /* eslint-disable-line */

	const response = await request( 'inbox/changes', {
		method: 'POST',
		body: {
			gflow_access_token:
				data[ gridId ]?.current_user_token || null,
			current_ids, /* eslint-disable-line */
			search_args: JSON.stringify(
				data[ gridId ]?.grid_options?.searchArgs || null
			),
		},
	} );

	state.latestUpdateData = response.data;
	handleUpdateCounts( gridId, response.data );
	response.data.addIndex = 0;
	instances.gridOptions[ gridId ].api.applyTransaction( response.data );
};

/**
 * @function setGridRefreshInterval
 * @description Poll grids for fresh data on a set interval and perform update operations.
 *
 * @since 2.8.0
 *
 */

const setGridRefreshInterval = () => {
	el.containers.forEach( ( grid ) => {
		if ( ! data[ grid.dataset.gridId ].fetch_enabled ) {
			return;
		}

		instances.refreshTimers[ grid.dataset.gridId ] = setInterval( () => {
			refreshGrid( grid.dataset.gridId ).then( () => {
				maybeHandleNotifications( grid.dataset.gridId );
			} );
		}, data[ grid.dataset.gridId ].fetch_interval * 1000 );
	} );
};

/**
 * @function bindEvents
 * @description Bind event handlers.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {};

/**
 * @function init
 * @description Kick off this modules functionality.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
	setGridRefreshInterval();
};

export default init;
