import instances from './instances';
import { arrayEquals, localStorage, getNodes } from '@gravityforms/utils';

import options from './options';
import state from './state';

/**
 * @function setGridInitialState
 * @description Sets the initial state for the grid, if no stored settings yet, sizes to current layout.
 *
 * @since 2.8.0
 *
 * @param {string} gridId The ag grid id string
 */

export const setGridInitialState = ( gridId ) => {
	let gridState = localStorage.get( gridId );

	if ( ! gridState ) {
		instances.gridOptions[ gridId ].api.sizeColumnsToFit();
		return;
	}

	const storedColumns = JSON.parse( gridState )
		.map( ( entry ) => entry.colId )
		.sort();
	const currentColumns = instances.gridOptions[ gridId ].columnDefs
		.map( ( entry ) => entry.field )
		.sort();

	if ( ! arrayEquals( storedColumns, currentColumns ) ) {
		instances.gridOptions[ gridId ].api.sizeColumnsToFit();
		return;
	}

	gridState = JSON.parse( gridState ).sort( ( x ) =>
		x.colId === 'id' ? -1 : 0
	);
	instances.gridOptions[ gridId ].columnApi.applyColumnState( {
		state: gridState,
		applyOrder: true,
	} );
};

/**
 * @function handleGridStateChange
 * @description Handles grid state changes and stores them in local storage
 *
 * @since 2.8.0
 *
 * @param {Event}  event  The ag grid event object
 * @param {string} gridId The ag grid id string
 */

export const handleGridStateChange = ( event, gridId ) => {
	const filters = instances.gridOptions[ gridId ].api.getFilterModel();
	const gridNode = getNodes(
		`[data-js="gflow-inbox"][data-grid-id="${ gridId }"]`,
		false,
		document,
		true
	)[ 0 ];
	if ( Object.keys( filters ).length === 0 ) {
		gridNode.classList.remove( 'gflow-inbox--filters-active' );
	} else {
		gridNode.classList.add( 'gflow-inbox--filters-active' );
	}
	localStorage.put(
		gridId,
		JSON.stringify(
			instances.gridOptions[ gridId ].columnApi.getColumnState()
		)
	);
};

/**
 * @function trackKeyState
 * @description Track whether desired keys are being pressed and set state as needed.
 *
 * @since 2.8.0
 *
 */

const trackKeyState = ( e ) => {
	if ( options.openNewTabKeycodes.indexOf( e.code ) === -1 ) {
		return;
	}
	state.openInNewTab = e.type === 'keydown';
};

/**
 * @function bindEvents
 * @description Bind event handlers.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	document.addEventListener( 'keydown', trackKeyState );
	document.addEventListener( 'keyup', trackKeyState );
};

/**
 * @function init
 * @description Kick off this module's functionality.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
};

export default init;
