import $ from 'jquery';
import { delegate, getNodes, getClosest, getCoords, focusLoop } from '@gravityforms/utils';
import { deleteRow } from '../utils/data';
import { redirectToEntryEdit } from '../utils/url';
import * as inboxTemplates from 'templates/grids/inbox';

import state from './state';
import instances from './instances';
import el from './elements';

const gflowStrings = window.gravityflow_inbox_strings || {};

/**
 * @class ActionsRenderer
 * @description Injects the quick actions menu for a row that has it enabled.
 *
 * @since 2.8.0
 *
 * @type {ActionsRenderer}
 */

export const actionsRenderer = class ActionsRenderer {
	init( params ) {
		const actionData = params.value;
		const cell = document.createElement( 'div' );
		cell.innerHTML = `
			<a class="gflow-inbox__entry-cell-link" href="${ params.node.data.url_entry }"></a>
		`;
		if ( ! actionData ) {
			this.eGui = cell;
			return;
		}
		if ( ! actionData.actions.length ) {
			this.eGui = cell;
			return;
		}
		state.actionData.push( {
			entryId: actionData.entryId,
			entryUrl: params.data.url_entry,
			actions: actionData.actions,
		} );
		this.eGui = document.createElement( 'div' );
		this.eGui.classList.add( 'gflow-grid__actions-container' );
		this.eGui.innerHTML = inboxTemplates.actionsTrigger(
			actionData.entryId,
			params.data.url_entry,
		);
	}

	getGui() {
		return this.eGui;
	}
};

/**
 * @function setAjaxLoading
 * @description Sets up the ui for quick actions ajax.
 *
 * @since 2.8.0
 *
 */

const setAjaxLoading = () => {
	el.activeActionMenu.classList.add(
		'gflow-grid__actions-menu--ajax-running'
	);
	el.activeActionMenu.classList.add(
		`gflow-grid__actions-menu--ajax-action-${ state.activeActionSubmitType }`
	);
	document.body.insertAdjacentHTML(
		'beforeend',
		inboxTemplates.loadingMask()
	);
};

/**
 * @function cleanUpAjaxUi
 * @description Cleans html elements up at the end of ajax save.
 *
 * @since 2.8.0
 *
 * @param  mask {HTMLElement} The mask element to remove.
 */

const cleanUpAjaxUi = ( mask ) => {
	mask.parentNode.removeChild( mask );
	if ( ! el.activeActionMenu ) {
		return;
	}
	el.activeActionMenu.classList.remove(
		'gflow-grid__actions-menu--ajax-running'
	);
	el.activeActionMenu.classList.remove(
		`gflow-grid__actions-menu--ajax-action-${ state.activeActionSubmitType }`
	);
};

/**
 * @function finishAjaxLoading
 * @description Handle ajax finish for quick actions action.
 *
 * @since 2.8.0
 *
 * @param  success {boolean} Was ajax successful?
 */

const finishAjaxLoading = ( success = true ) => {
	const mask = getNodes( 'gform-loading-mask' )[ 0 ];
	if ( success ) {
		el.activeActionMenu.classList.add(
			'gflow-grid__actions-menu--ajax-success'
		);
		setTimeout( () => {
			el.activeActionMenu.classList.add(
				'gflow-grid__actions-menu--fade-out'
			);
			setTimeout( () => {
				if ( state.activeActionKey === 'Enter' && state.activeRowNode.nextElementSibling ) {
					const cellToFocus = getNodes( '.ag-cell', false, state.activeRowNode.nextElementSibling, true )[ 0 ];
					cellToFocus.focus( { preventScroll: true } );
				}
				deleteRow(
					instances.gridOptions[ state.activeGrid ],
					state.activeActionMenuEntryId
				);
				cleanUpAjaxUi( mask );
				closeQuickActionMenu();
			}, 400 );
		}, 600 );
	} else {
		cleanUpAjaxUi( mask );
	}
};

/**
 * @function submitNoteAction
 * @description Ajax to submit the current action.
 *
 * @since 2.8.0
 *
 */

const submitNoteAction = () => {
	$.ajax( {
		method: 'POST',
		url: `${ gflowStrings?.restUrl }gf/v2/entries/${ state.activeActionMenuEntryId }/workflow/approvals`,
		data: {
			action: state.activeActionSubmitType,
			gravityflow_note: el.activeActionMenuNote.value,
		},
		beforeSend( xhr ) {
			xhr.setRequestHeader( 'X-WP-Nonce', gflowStrings?.nonce );
			setAjaxLoading();
		},
		success( response ) {
			if ( response.status === 'success' ) {
				finishAjaxLoading();
			} else {
				finishAjaxLoading( false );
				alert( response.feedback ); // eslint-disable-line
			}
		},
		fail( response ) {
			finishAjaxLoading( false );
			alert( response ); // eslint-disable-line
		},
	} );
};

/**
 * @function handleQuickAction
 * @description Handles approve or reject actions for a row.
 *
 * @since 2.8.0
 *
 * @param  requiresNote {string} String to set whether action requires note.
 */

const handleQuickAction = ( requiresNote = '' ) => {
	if ( requiresNote === 'true' ) {
		el.activeActionMenuNote.focus();
		el.activeActionMenu.classList.add(
			'gflow-grid__actions-menu--note-active'
		);
	} else {
		submitNoteAction();
	}
};

/**
 * @function handleQuickActionsMenuTrigger
 * @description Handle the trigger events for a quick actions menu instance.
 *
 * @since 2.8.0
 *
 * @param  e The event object.
 */

const handleQuickActionsMenuTrigger = ( e ) => {
	const trigger = e.delegateTarget;
	const action = trigger.dataset.action;
	const requiresNote = trigger.dataset.requiresNote;

	if ( action === 'edit' ) {
		redirectToEntryEdit( trigger.dataset.url );
	} else if ( action === 'approve' || action === 'reject' ) {
		state.activeActionSubmitType = action;
		handleQuickAction( requiresNote, trigger );
	}
};

/**
 * @function handleQuickActionsMenuNote
 * @description Handle key events on the note textarea for an action. Store content to state, and enable/disable based on length.
 *
 * @since 2.8.0
 *
 */

const handleQuickActionsMenuNote = () => {
	el.activeActionMenuSubmit.disabled =
		el.activeActionMenuNote.value.trim().length <= 0;
};

/**
 * @function handleQuickActionsMenuNoteAction
 * @description Cancel or submit the current note for an entry.
 *
 * @since 2.8.0
 *
 */

const handleQuickActionsMenuNoteAction = ( e ) => {
	const trigger = e.delegateTarget;
	if ( trigger.dataset.action === 'cancel' ) {
		el.activeActionMenuNote.value = '';
		el.activeActionMenuSubmit.disabled = true;
		el.activeActionMenu.classList.remove(
			'gflow-grid__actions-menu--note-active'
		);
		return;
	}

	submitNoteAction();
};

/**
 * @function positionQuickActionMenu
 * @description Set the position of the quick actions menu to be directly below the trigger.
 *
 * @since 2.8.0
 *
 */

const positionQuickActionMenu = () => {
	const adminBar = document.getElementById( 'wpadminbar' );
	const barOffset = adminBar ? adminBar.offsetHeight : 0;
	const offset = getCoords( state.activeAction );
	const spaceBelow = window.innerHeight - offset.bottom;

	if ( spaceBelow - el.activeActionMenu.offsetHeight < 20 ) {
		const topOffset = document.body.classList.contains(
			'gflow-grid--fullscreen-active'
		)
			? state.activeAction.offsetHeight
			: 0;
		el.activeActionMenu.style.marginTop = `-${
			topOffset + el.activeActionMenu.offsetHeight
		}px`;
		el.activeActionMenu.style.top = `${
			offset.top + state.activeAction.offsetHeight - barOffset
		}px`;
	} else {
		const bottomOffset = document.body.classList.contains(
			'gflow-grid--fullscreen-active'
		)
			? state.activeAction.offsetHeight
			: state.activeAction.offsetHeight * 2;
		el.activeActionMenu.style.top = `${
			offset.top + bottomOffset - barOffset
		}px`;
	}
	el.activeActionMenu.style.left = `${ offset.left }px`;
};

/**
 * @function handleKeyEvents
 * @description Handles accessibility focus looping on the menu using the focusLoop util.
 *
 * @param {KeyboardEvent} e
 */

const handleKeyEvents = ( e ) =>
	focusLoop(
		e,
		state.activeAction,
		el.activeActionMenu,
		closeQuickActionMenu
	);

/**
 * @function openQuickActionMenu
 * @description Inject quick actions menu to dom, appended to body as ag grid uses translate for positioning.
 *
 * @since 2.8.0
 *
 * @param  entryId    {number} The entry id.
 * @param  actionData {object} The actiondata object.
 * @param  entryUrl   {string} The url to the details screen
 * @param  e          The event object.
 */

const openQuickActionMenu = ( entryId, actionData, entryUrl, e ) => {
	state.actionMenuOpen = true;
	state.activeActionMenuEntryId = entryId;
	state.activeActionMenuEntryUrl = entryUrl;
	const menu = inboxTemplates.actionsMenu( entryUrl, actionData.actions );
	document.body.insertAdjacentHTML( 'beforeend', menu );
	el.activeActionMenu = getNodes( 'gflow-quick-actions-menu' )[ 0 ];
	el.activeActionMenuNote = getNodes(
		'gflow-quick-actions-menu-note',
		false,
		el.activeActionMenu
	)[ 0 ];
	el.activeActionMenuSubmit = getNodes(
		'[data-js="gflow-quick-actions-menu-note-action"][data-action="submit"]',
		false,
		el.activeActionMenu,
		true
	)[ 0 ];
	if ( e.key === 'Enter' ) {
		getNodes( 'gflow-quick-actions-menu-trigger', false, el.activeActionMenu )[ 0 ].focus( { preventScroll: true } );
	}
	positionQuickActionMenu();
	el.activeActionMenu.addEventListener( 'keydown', handleKeyEvents );
};

/**
 * @function closeQuickActionMenu
 * @description Remove the quick actions menu from dom.
 *
 * @since 2.8.0
 *
 */

const closeQuickActionMenu = () => {
	el.activeActionMenu.parentNode.removeChild( el.activeActionMenu );
	state.activeAction = null;
	el.activeActionMenu = null;
	state.actionMenuOpen = false;
};

/**
 * @function handleQuickActions
 * @description Handle all state and rendering for displaying the quick actions menu.
 *
 * @since 2.8.0
 *
 */

export const handleQuickActions = ( e ) => {
	const trigger = e.delegateTarget;
	const entryId = Number( trigger.dataset.id );
	const entryUrl = trigger.dataset.url;
	const actionData = state.actionData.filter(
		( d ) => Number( d.entryId ) === entryId
	)[ 0 ];
	state.activeActionKey = e.key;
	state.activeGridNode = getClosest(
		trigger,
		'[data-js="gflow-inbox"]'
	);
	state.activeRowNode = getClosest(
		trigger,
		'.ag-row'
	);
	state.activeGrid = state.activeGridNode.dataset.gridId;
	// another menu is open, close that one first
	if ( state.actionMenuOpen && state.activeAction !== trigger ) {
		closeQuickActionMenu();
	}

	if ( state.actionMenuOpen ) {
		closeQuickActionMenu();
	} else {
		state.activeAction = trigger;
		openQuickActionMenu( entryId, actionData, entryUrl, e );
	}
};

/**
 * @function maybeCloseQuickActions
 * @description Handle body clicks. If not inside the active menu, close that menu.
 *
 * @since 2.8.0
 *
 * @param  e {Event} The event object.
 */

const maybeCloseQuickActions = ( e ) => {
	if ( ! state.actionMenuOpen ) {
		return;
	}
	if (
		! (
			e.target === getNodes( 'gform-loading-mask' )[ 0 ] ||
			e.target === state.activeAction ||
			el.activeActionMenu.contains( e.target )
		)
	) {
		closeQuickActionMenu();
	}
};

/**
 * @function handleResize
 * @description Handle resize events, not debounced here for fluid updates, be careful and watch perf!
 *
 * @since 2.8.0
 *
 */

const handleResize = () => {
	if ( state.actionMenuOpen ) {
		positionQuickActionMenu();
	}
};

/**
 * @function bindEvents
 * @description Bind event handlers.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {
	delegate(
		document.body,
		'[data-js="gflow-quick-actions-trigger"]',
		'click',
		handleQuickActions
	);

	delegate(
		document.body,
		'[data-js="gflow-quick-actions-menu-trigger"]',
		'click',
		handleQuickActionsMenuTrigger
	);

	delegate(
		document.body,
		'[data-js="gflow-quick-actions-menu-note"]',
		'keyup',
		handleQuickActionsMenuNote
	);

	delegate(
		document.body,
		'[data-js="gflow-quick-actions-menu-note-action"]',
		'click',
		handleQuickActionsMenuNoteAction
	);

	document.addEventListener( 'click', maybeCloseQuickActions );
	window.addEventListener( 'resize', handleResize );
};

/**
 * @function init
 * @description Kick off this module's functionality.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	bindEvents();
};

export default init;
