import gflowConfig from 'gflow-config';
import { localStorage } from '@gravityforms/utils';
import request from '../../../utils/request';

const gflowStrings = window.gravityflow_inbox_strings || {};

/**
 * @function getPref
 * @description Get preference from database by key using our request library.
 *
 * @since 2.8.0
 *
 * @param  key          {string}
 * @param  type         {string}
 * @param  view         {string}
 * @param  defaultValue {string}
 *
 * @return {Promise<*>}
 */

export const getPref = async ( key, type, view, defaultValue ) => {
	return gflowConfig.is_user_logged_in ? await request( 'inbox/preferences', {
		method: 'GET',
		headers: {
			'X-WP-Nonce': gflowStrings?.nonce,
		},
		params: {
			key,
			type,
			user_id: gflowConfig.user_id,
			view,
			default: defaultValue,
		},
	} ) : new Promise( ( resolve ) => {
		resolve( localStorage.get( `${ view }_setting_${ key }` ) );
	} );
};

/**
 * @function savePref
 * @description Save preference in database by key using our request library.
 *
 * @since 2.8.0
 *
 * @param  key   {string}
 * @param  value {string}
 * @param  type  {string}
 * @param  view  {string}
 *
 * @return {Promise<*>}
 */

export const savePref = async ( key, value, type, view ) => {
	return gflowConfig.is_user_logged_in ? await request( 'inbox/preferences', {
		method: 'PUT',
		headers: {
			'X-WP-Nonce': gflowStrings?.nonce,
			'Content-type': 'application/json; charset=UTF-8',
		},
		json: {
			key,
			value,
			type,
			user_id: gflowConfig.user_id,
			view,
		},
	} ) : new Promise( ( resolve ) => {
		localStorage.put( `${ view }_setting_${ key }`, value );
		resolve( {
			key,
			value,
			type,
			user_id: gflowConfig.user_id,
			view,
		} );
	} );
};
